* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  padding: 20px;
  font-weight: 100;
}

#header {
  border: 1px solid;
  background-color: rgb(145, 145, 145);
  /* width: 100%; */
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 5px black;
  position: sticky;
  top: 0;
}

#logo {
  font-size: 25px;
  margin: 0px 10px;
}


#navbar ul {
  display: flex;
  margin-right: 30px;
}

#navbar ul li a {
  display: block;
  border-radius: 50px;
  text-decoration: none;
  font-size: 22px;
  color: black;
  padding: 10px 20px;
}

#navbar ul li a:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  transition: 0.3s;
}

#navbar ul li a:active {
  background-color: rgb(255, 255, 255);
}

#navbar ul li {
  list-style: none;
  padding: 0px 10px;
}

#burger {
  /* border: 2px solid; */
  border-radius: 8px;
  width: 50px;
  height: 38px;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: 0.2s;
}

.burger-breads {
  border: 2px solid;
  width: 35px;
  transition: 0.2s;
}

#burger:hover {
  border: 2px solid white;
}

#burger:hover .burger-breads {
  border: 2px solid white;
}

@media only screen and (max-width:640px) {
  #burger {
      display: flex;
      margin-right: 20px;
  }

  #navbar {
      position: absolute;
      top: 60px;
      left: 0;
      right: 0;
      height: 0;
      background-color: rgb(174, 173, 173);
      transition: 0.3s;
      overflow: hidden;
  }

  #navbar.active {
      height: 276px;
  }

  #navbar.active ul li Link {
      display: block;
  }

  #navbar ul {
      display: block;
      text-align: center;
  }

  #navbar ul li {
      margin: 20px 10px;
  }
  
  #navbar ul li Link {
      transition: 0.2s;
      display: none;
  }

  #burger.active{
      justify-content: center;
      align-items: center;
      border: 2px solid white;

    }
  #burger.active #bb-1{
      transform: translate(3%,61%)  rotate(45deg);
      border: 2px solid white;
    }
    #burger.active #bb-2{
      display: none;
    }
    #burger.active #bb-3{
      transform: translate(3%,-38%)  rotate(-45deg); 
      border: 2px solid white;   
    }
}

